@import "../../../fonts/Montserrat/fonts.css";
@import "../../../fonts/Quicksand/fonts.css";
@import "../../../fonts/Open-Sans/fonts.css";
* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

html {
  background: #e2e2e2;
  color: #1f2041;
  font-size: 14px;
  font-family: montserrat, Arial, sans-serif;
}

body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

h1,
h2,
h3,
label {
  font-family: montserrat, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  color: #1f2041;
  text-transform: uppercase;
}

input + label {
  font-weight: normal;
  text-transform: none;
}

h1,
legend {
  font-size: 24px;
  line-height: 30px;
  font-family: open-sans, Arial, sans-serif;
  font-weight: bold;
  color: #1f2041;
}

h2 {
  font-size: 19px;
  font-family: open-sans, Arial, sans-serif;
}

input,
textarea,
select,
button {
  font: inherit;
  outline: none;
}

form {
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

fieldset {
  border: 0 none;
}

.common__input_hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.common__button_reset {
  border: 0;
  background: transparent;
}

ul {
  list-style: none;
}

.color-example {
  display: flex;
}

.color-example__squareColor {
  width: 70px;
  height: 70px;
  border-radius: 6px;
  margin-right: 40px;
}

.color-example__squareColor_DS100 {
  background: #1f2041;
}
.color-example__squareColor_DS75 {
  background: rgba(31, 32, 65, 0.75);
}
.color-example__squareColor_DS50 {
  background: rgba(31, 32, 65, 0.5);
}
.color-example__squareColor_DS25 {
  background: rgba(31, 32, 65, 0.25);
}
.color-example__squareColor_DS5 {
  background: rgba(31, 32, 65, 0.05);
}
.color-example__squareColor_Purple {
  background: #bc9cff;
}
.color-example__squareColor_Green {
  background: #6fcf97;
}

.color-example .color-example__textDesc {
  vertical-align: middle;
  color: #1f2041;
  font-family: quicksand, Arial, sans-serif;
  font-size: 19px;
  line-height: 29px;
  margin: 6px 0px;
}

.color-example__title {
  font-weight: bold;
}

.color-example__title, .color-example__text {
  font-family: quicksand, Arial, sans-serif;
}

.font-example {
  display: flex;
}

.font-example__title {
  font-family: montserrat, Arial, sans-serif;
  margin-right: 30px;
  color: rgba(31, 32, 65, 0.25);
  text-align: right;
  width: 50px;
}

.font-example__desc {
  color: #1f2041;
  font-family: inherit;
}

.font-example_h1 {
  font-family: quicksand, Arial, sans-serif;
  font-size: 24px;
  line-height: 30px;
}
.font-example_h1 .font-example__desc {
  width: 351px;
  font-weight: bold;
}
.font-example_h2 {
  font-family: quicksand, Arial, sans-serif;
  font-size: 19px;
  line-height: 24px;
}
.font-example_h2 .font-example__desc {
  width: 282px;
  font-weight: bold;
}
.font-example_h3 {
  font-family: montserrat, Arial, sans-serif;
  font-size: 12px;
  line-height: 15px;
}
.font-example_h3 .font-example__desc {
  width: 186px;
  font-weight: bold;
}
.font-example_body {
  font-family: montserrat, Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
}
.font-example_body .font-example__desc {
  width: 301px;
  color: rgba(31, 32, 65, 0.75);
}

body {
  margin: 0px auto;
  padding: 145px 150px;
  display: flex;
  min-width: 320px;
  max-width: 1440px;
  background: white;
}

.color-example__wrapper {
  margin-right: 416px;
}

.color-example:not(:last-of-type) {
  margin-bottom: 20px;
}

.font-example:not(:last-of-type) {
  margin-bottom: 50px;
}